.Projectcard {
  /* border: 2px solid mediumseagreen; */
  width: 300px;
  padding: 10px;
  margin: 1rem;
  box-shadow: 7px 10px 12px -5px rgba(0, 0, 0, 0.56);
  text-align: center;
  background-color: white;
  border-radius: 3px;
}

.Projectcard-data {
  font-size: 80%;
}

.Projectcard-title {
  margin-bottom: 1rem;
  font-weight: bold;
  font-size: 1.5rem;
}

.Projectcard-image {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 250px;
  background-color: #eceff1;
  border-radius: 3px;
  margin-bottom: 1rem;
}
.Projectcard img {
  transition: all ease 500ms;
}
.Projectcard img:hover {
  transform: scale(1.25);
  opacity: 0.7;
}

/* image show modified- eliminated pixresize and circle crop */
img {
  width: 215px;
  height: 215px;
  border-radius: 50%;
}
